






























export default {
    data() {
        return {
            title: 'addPhoneContact',
            name: '',
            phone: ''
        }
    },
    methods: {
        nameChange: function(e) {
            this.name = e.detail.value
        },
        phoneChange: function(e) {
            this.phone = e.detail.value
        },
        add() {
            uni.addPhoneContact({
                firstName: this.name,
                mobilePhoneNumber: this.phone,
                success: function() {
                    uni.showModal({
                    	content:'已成功添加联系人！',
                        showCancel:false
                    })
                },
                fail: function() {
                    uni.showModal({
                    	content:'添加联系人失败！',
                        showCancel:false
                    })
                }
            });
        }
    }
}
